<template>
  <v-container>
    <v-card height="100%" flat>
      <v-card-title> </v-card-title>
      <v-card-text>
        <v-row style="height: 90%">
          <v-col cols="3">
            <v-list v-for="d in metadata" :key="d.name" nav rounded>
              <v-list-item @click="show(d)">
                <v-list-item-icon
                  ><v-icon> mdi-puzzle </v-icon>
                </v-list-item-icon>
                {{ d.name }}</v-list-item
              >
            </v-list>
          </v-col>
          <v-col>
            <v-card v-if="selectedItem" height="100%" flat>
              <v-container>
                <v-row>
                  <!-- <v-col cols="2"> <v-avatar style="background:gray" tile/> </v-col> -->
                  <v-col>
                    <h1>
                      {{ selectedItem.name }}
                    </h1>
                    <v-chip
                      outlined
                      color="primary"
                      x-small
                      v-for="t in selectedItem.tags"
                      :key="t"
                    >
                      {{ t }}
                    </v-chip>

                    <span>
                      {{ selectedItem.description }}
                    </span>
                  </v-col>
                </v-row>
                  <v-icon>mdi-github</v-icon>
                  <a :href="toGitHub(selectedItem.github)" target="_blank">{{selectedItem.github}}</a> 
                  <br/>
                  <br/>
                  <v-text-field
                    outlined
                    name="input-7-4"
                    label=""
                    readonly
                    :value="toCommand(selectedItem.name)"
                  ></v-text-field>
                  <v-textarea readonly filled>
                    
                  </v-textarea>
                </v-container>
            </v-card>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions></v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    metadata: null,
    selectedItem: null,
  }),
  mounted() {
    axios.get("metadata/zh/gc-plugin/index.json").then((res) => {
      this.metadata = res.data;
      console.log(res.data);
      this.selectedItem = res.data[0];

    });
  },
  methods: {
    show(d) {
      this.selectedItem = d;
    },
    toGitHub(g) {
      return `https://github.com/${g}`
      
    },
    toCommand(n){
      return `gc-cli addplu ${n}`
    }
  },
};
</script>

